import React, { useContext, useEffect, useState } from 'react'
import { Container, Dropdown, Nav, Navbar } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import { useNavigate } from 'react-router-dom'
import { useCart } from 'react-use-cart'
import { LanguageContext, LanguageProvider } from '../context/LaguageContext'
import { useWishlist } from 'react-use-wishlist'


const Header = () => {


    // Cart and Wishlist
    const { items } = useCart();
    const { totalWishlistItems } = useWishlist();
    const navigate = useNavigate();
    const [user, setUser] = useState('');

    const [directionProfile, setdirectionProfile] = useState('login');

    // Define Direction for Sign in / Sign Out Button
    const defineDirection = () => {
        if (user === 'Login') {
        } else {
            return (
                <button className='btn btn-secondary'
                    onClick={() => {
                        localStorage.setItem('currentUser', null);
                        navigate('/');
                        window.location.reload();
                    }}>
                    {buttons.sign_out}
                </button>
            )
        }
    }

    // Current User Definer
    const currentUser = () => {
        if (localStorage.getItem('currentUser') === null || localStorage.getItem('currentUser') === "null") {
            setUser("Login");
            setdirectionProfile('login')
        }
        else {
            setUser(localStorage.getItem('currentUser'))
            if (localStorage.getItem('currentUser') === "Admin") {
                setdirectionProfile('dashboard')
            } else {
                setdirectionProfile('cart')
            }

        }
    }

    // Language Context
    const [languageData] = useContext(LanguageContext);
    const [headerData, setHeaderData] = useState(languageData);
    const [buttons, setButtons] = useState({});
    // Language setter to Local Storage
    const [lang, setLang] = useState(localStorage.getItem("lang"));
    const langSetter = (e) => {
        localStorage.setItem("lang", e.currentTarget.textContent);
        window.location.reload();
    }


    useEffect(() => {
        currentUser();
        defineDirection();
        if (localStorage.getItem("lang") === null) {
            localStorage.setItem("lang", "EN");
        }
        if (languageData.header !== undefined) {
            setHeaderData(languageData.header)
        }
        if (languageData.buttons !== undefined) {
            setButtons(languageData.buttons)
        }
    },)

    return (
        <header>
            <Navbar className='navbar-dark py-4' expand="lg">
                <Container>
                    <Navbar.Brand >
                        <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/t/4/assets/logo.png?v=165080812783092027501535017716" alt="" />
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />

                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="mx-auto">
                            <LinkContainer to='/'>
                                <Nav.Link className='mx-2'> {headerData.home} </Nav.Link>
                            </LinkContainer>

                            <LinkContainer to='/shop'>
                                <Nav.Link className='mx-2'>{headerData.shop} </Nav.Link>
                            </LinkContainer>

                            <LinkContainer to='/blog'>
                                <Nav.Link className='mx-2'>{headerData.blog} </Nav.Link>
                            </LinkContainer>


                            <LinkContainer to='/men'>
                                <Nav.Link className='mx-2'>{headerData.men} </Nav.Link>
                            </LinkContainer>

                            <LinkContainer to='/women'>
                                <Nav.Link className='mx-2'>{headerData.women} </Nav.Link>
                            </LinkContainer>

                            <LinkContainer to='/about'>
                                <Nav.Link className='mx-2'>{headerData.about} </Nav.Link>
                            </LinkContainer>

                            <LinkContainer to='/contact'>
                                <Nav.Link className='mx-2'>{headerData.contact} </Nav.Link>
                            </LinkContainer>
                        </Nav>

                        <div className="navbarTool">

                            <div className="element" onClick={() => {
                                if (localStorage.getItem('currentUser') === null || localStorage.getItem('currentUser') === "null") {
                                    navigate('/login');
                                } else {
                                    navigate('/cart');
                                }
                            }}>
                                <i className="fa-solid fa-basket-shopping"></i>
                                <span className='cart-length'>{items.length}</span>
                            </div>

                            <LinkContainer to='/search'>
                                <div className="element">
                                    <i className="fa-solid fa-magnifying-glass"></i>
                                </div>
                            </LinkContainer>


                            <LinkContainer to='/wishlist'>
                                <div className="element">
                                    <i className="fa-regular fa-heart"></i>
                                    <span className='cart-length'>{totalWishlistItems}</span>
                                </div>
                            </LinkContainer>


                            <div className="element">
                                <Dropdown>
                                    <Dropdown.Toggle id="dropdown-basic">
                                        {lang}
                                    </Dropdown.Toggle>

                                    <Dropdown.Menu>
                                        <Dropdown.Item onClick={langSetter}>EN</Dropdown.Item>
                                        <Dropdown.Item onClick={langSetter}>AZE</Dropdown.Item>
                                    </Dropdown.Menu>
                                </Dropdown>
                            </div>

                            <LinkContainer to={`/${directionProfile}`}>
                                <div className="element">
                                    <i className="fa-solid fa-user"></i>
                                    <div className="currentUser">
                                        <p>{user}</p>
                                        {defineDirection()}
                                    </div>
                                </div>
                            </LinkContainer>

                        </div>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </header>
    )
}

export default Header

