import React, { useContext, useEffect, useState } from 'react'
import HeroPart from '../components/HeroPart'
import watches_data from '../data/WatchesData';
import SingleCard from '../components/SingleCard';
import { LanguageContext } from '../context/LaguageContext';
import { useWishlist } from 'react-use-wishlist';

const Search = () => {
    const {getWishlistItem} = useWishlist();
    const [searchInput, setSeacrhInput] = useState();

    // Language Context
    const [languageData] = useContext(LanguageContext);

    const [searchLanguage, setSearchLanguage] = useState({});

    useEffect(() => {
        if (languageData.searchPage !== undefined) {
            setSearchLanguage(languageData.searchPage);
        }
    },)

    return (
        <section className='searchPage'>
            <HeroPart subHead={searchLanguage.page_head_title} textHead={searchLanguage.page_head_title} />

            <div className="container">

                <div className="input-container">
                    <input type="search" name="" id="" onChange={(e) => setSeacrhInput(e.target.value)} placeholder={searchLanguage.input_placeholder} />
                </div>

                <div className="row g-5">
                    {
                        watches_data.filter(cd => cd.title.toLocaleLowerCase().includes(searchInput)).map((item) => {
                            var heartColor = "";
                            var heartStyle = "";

                            if (getWishlistItem(item.id) != undefined) {
                                heartColor = 'danger';
                                heartStyle = 'solid';
                            } else {
                                heartStyle = 'regular'
                            }
                            return (
                                <div className="col-12 col-sm-6 col-md-4 col-lg-3">
                                    <SingleCard
                                        key={item.id}
                                        id={item.id}
                                        main_img={item.main_img}
                                        second_img={item.second_img}
                                        price={item.price}
                                        prev_price={item.prev_price}
                                        title={item.title}
                                        currencyIcon={"$"}
                                        allData={item}
                                        heartColor={heartColor}
                                        heartStyle={heartStyle}
                                    />
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        </section>
    )
}

export default Search