import Alert from '../components/Alert';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'

const Login = () => {

  // Alert Setting
  const [alertText, setAlertText] = useState("");
  const [alertType, setAlertType] = useState("");
  //


  const navigate = useNavigate();
  const users = [
    {
      user: "Admin",
      login: "admin.admin@mail.ru",
      password: "admin123"
    },
    {
      user: "Baloglan",
      login: "baloglan@mail.ru",
      password: "baloglan123"
    },
    {
      user: "Hafiz",
      login: "hafiz@mail.ru",
      password: "hafiz123"
    },
  ]
  const [inputEmail, setInputEmail] = useState("");
  const [inputPass, setInputPass] = useState("");

  const emailChange = (e) => {
    setInputEmail(e.target.value)
  }
  const passChange = (e) => {
    setInputPass(e.target.value)
  }
  
  const verifiedUser = users.find(correct => correct.login === inputEmail);


  const formSubmit = (e) => {
    e.preventDefault();
    if (inputEmail === "" || inputPass === "") {
      setAlertText("Please fill the gap");
      setAlertType("warning");
    } else {
      console.log(verifiedUser);
      if(verifiedUser == undefined){
        setAlertText("Wrong password or email!!!")
        setAlertType("danger")
      }else{
        if (inputEmail === verifiedUser.login && inputPass === verifiedUser.password) {
          setAlertText("Log in successfully !!!")
          setAlertType("success")
          localStorage.setItem('currentUser', verifiedUser.user);
          if (verifiedUser.user === "Admin") {
            navigate('/dashboard');
          }
          else {
            navigate('/cart');
          }
        } else {
          setAlertText("Wrong password or email!!!")
          setAlertType("danger")
        }
      }
      
    }
  }


  return (
    <section className='login-page'>
      <section className='page-head'>
        <h4>Account</h4>
        <p> <span> Home </span> /  <span>Account</span></p>
      </section>


      <Alert alertText={alertText} alertType={alertType} />
      <div className="login-body">
        <div className="col-8 col-sm-6 col-md-3">
          <form onSubmit={formSubmit}>
            <div className="inputs-main">
              <input type="email" name="" id="" placeholder='Email' onChange={emailChange} />
              <input type="password" name="" id="" placeholder='Password' onChange={passChange} />

              <button className='blue-bordered-btn'>
                <div className="top-frame"></div>
                Sign In
                <div className="bottom-frame"></div>
              </button>

            </div>
          </form>
        </div>
      </div>
    </section >
  )
}

export default Login