import HeroPart from '../components/HeroPart'
import { useParams } from 'react-router-dom'
import watches_data from '../data/WatchesData';
import { useCart } from 'react-use-cart';
import { useWishlist } from 'react-use-wishlist';


const ProductDetail = () => {
    const { addItem } = useCart();
    const { url } = useParams();

    const { getWishlistItem , removeWishlistItem } = useWishlist();

    const { addWishlistItem } = useWishlist();

    
    const currentProducts = watches_data.find(item => item.id.toString(10) === url);

    
    return (
        <section className='productDetail'>
            <HeroPart subHead="All Products" textHead={currentProducts.title} />

            <div className="details-main">
                <div className="container">
                    <div className="row g-5">
                        <div className="col-12 col-sm-12 col-md-6 col-lg-4">
                            <div className="left-info">

                                <h2 className='text-head'>{currentProducts.title}</h2>
                                <h3 className='text-price'>${currentProducts.price}</h3>
                                <p className='stars'>
                                    <i className="fa-regular fa-star"></i>
                                    <i className="fa-regular fa-star"></i>
                                    <i className="fa-regular fa-star"></i>
                                    <i className="fa-regular fa-star"></i>
                                    <i className="fa-regular fa-star"></i>
                                </p>

                                <p className='desc-short'>
                                    {currentProducts.desc.slice(0, 240)} ...
                                </p>

                                <div className="product-infos">
                                    <div className="info-item">
                                        <p>Price Range:</p>
                                        <p>$329.00$489.00</p>
                                    </div>

                                    <div className="info-item">
                                        <p>Product Type:</p>
                                        <p>Watches</p>
                                    </div>

                                    <div className="info-item">
                                        <p>Availability:</p>
                                        <p className='stock-status'>Many in Stock</p>
                                    </div>
                                </div>

                            </div>
                        </div>

                        <div className="col-12 col-sm-12 col-md-6 col-lg-5">
                            <div className="pictures-main">
                                <img src={currentProducts.main_img} alt="" />
                                <img className='mt-5' src={currentProducts.second_img} alt="" />
                            </div>
                        </div>

                        <div className="col-12 col-sm-12 col-md-6 col-lg-3">
                            <div className="right-info">
                                <div className="options-main">
                                    <p className='option-head'>Size:</p>

                                    <div className="buttons-main">
                                        <button className='size-btn'>L</button>
                                        <button className='size-btn'>M</button>
                                        <button className='size-btn'>S</button>
                                    </div>
                                </div>

                                <div className="options-main">
                                    <p className='option-head'>Color:</p>

                                    <div className="buttons-main">
                                        <button className='color-btn'></button>
                                    </div>
                                </div>

                                <div className="options-main">
                                    <p className='option-head'>Material:</p>

                                    <div className="buttons-main">
                                        <button>Rubber</button>
                                    </div>
                                </div>

                                <div className="options-main mt-5">
                                    <button className='blue-bordered-btn' onClick={() => addItem(currentProducts)}>
                                        <div className="top-frame"></div>
                                        <i className="fa-solid fa-cart-shopping me-1"></i>
                                        Add to Cart
                                        <div className="bottom-frame"></div>

                                    </button>

                                </div>

                                <div className="options-main mt-5">
                                    <button className='blue-bordered-btn' onClick={
                                        () => {
                                            if (getWishlistItem(currentProducts.id) !== undefined) {
                                                removeWishlistItem(currentProducts.id);
                                            } else {
                                                addWishlistItem(currentProducts);
                                            }
                                        }
                                    }>
                                        <div className="top-frame"></div>
                                        {
                                            getWishlistItem(currentProducts.id) !== undefined ?
                                            <i className={`fa-solid fa-heart me-1 text-danger`}></i>
                                            :
                                            <i className={`fa-regular fa-heart me-1 text-white`}></i>
                                            
                                        }
                                        Add to Wishlist
                                        <div className="bottom-frame"></div>

                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default ProductDetail