import React, { useContext, useEffect, useState } from 'react'
import HeroPart from '../components/HeroPart'
import { LanguageContext } from '../context/LaguageContext';

const About = () => {

  // Language Context
  const [languageData] = useContext(LanguageContext);

  const [aboutData, setAboutData] = useState({});
  const [advantages, setAdvantages] = useState({});
  const [buttons, setButtons] = useState({});
  useEffect(() => {
    if (languageData.aboutPage !== undefined) {
      setAboutData(languageData.aboutPage);
    }
    // 
    if (aboutData.advantages !== undefined) {
      setAdvantages(aboutData.advantages)
    }
    //
    if (languageData.buttons !== undefined) {
      setButtons(languageData.buttons);
    }
  },)

  return (
    <section className='aboutPage'>
      <HeroPart textHead={aboutData.page_head_title} subHead={aboutData.page_head_subTitle} />

      {/* BOTTOM OF HERO SECTION START */}
      <div className="bottom-of-hero">
        <div className="row">
          <div className="col-12 col-sm-12 col-md-6" id='left'>
            <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/img-2_b3f955cc-c3d3-48b6-98f7-94d85ca74bca_1920X.png?v=1613530999" alt="" />
          </div>

          <div className="col-12 col-sm-12 col-md-6" id='right'>
            <div className="main-box">
              <div className="row g-3">

                <div className="col-12 col-sm-6">
                  <div className="item-main">
                    <div className="icon">
                      <i className="fa-solid fa-droplet"></i>
                    </div>

                    <div className="text-box">
                      <h3>{advantages.item_1_title}</h3>
                      <p>{advantages.item_1_text}</p>
                    </div>
                  </div>
                </div>

                <div className="col-12 col-sm-6">
                  <div className="item-main">
                    <div className="icon">
                      <i className="fa-solid fa-leaf"></i>
                    </div>

                    <div className="text-box">
                      <h3>{advantages.item_2_title}</h3>
                      <p>{advantages.item_2_text}</p>
                    </div>
                  </div>
                </div>

                <div className="col-12 col-sm-6">
                  <div className="item-main">
                    <div className="icon">
                      <i className="fa-solid fa-house"></i>
                    </div>

                    <div className="text-box">
                      <h3>{advantages.item_3_title}</h3>
                      <p>{advantages.item_3_text}</p>
                    </div>
                  </div>
                </div>

                <div className="col-12 col-sm-6">
                  <div className="item-main">
                    <div className="icon">
                      <i className="fa-regular fa-clock"></i>
                    </div>

                    <div className="text-box">
                      <h3>{advantages.item_4_title}</h3>
                      <p>{advantages.item_4_text}</p>
                    </div>
                  </div>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
      {/* BOTTOM OF HERO SECTION END */}

      {/* Services-Part SECTION START */}
      <div className="services-part">
        <div className="box-main">
          <div className="buttons-div">
            <button className='blue-bordered-btn'>
              <div className="top-frame"></div>
              {buttons.service}
              <div className="bottom-frame"></div>

            </button>

            <button className='blue-bordered-btn'>
              <div className="top-frame"></div>
              {buttons.our_quality}
              <div className="bottom-frame"></div>

            </button>

            <button className='blue-bordered-btn'>
              <div className="top-frame"></div>
              {buttons.support}
              <div className="bottom-frame"></div>

            </button>
          </div>

          <div className="text-container">
            <p>
              {aboutData.container_text_1}
            </p>

            <p>
              {aboutData.container_text_2}
            </p>
          </div>
        </div>
      </div>
      {/* Services-Part SECTION END */}

      {/* Blogs SECTION Start */}
      <div className="blogers-main container-fluid">
        <div className="row g-4">
          <div className="col-12 col-sm-6 col-md-4 col-lg-3">
            <div className="bloger-card">
              <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/team-2_74948b63-d0e6-4f26-a8fb-b360055fd845_1024x1024.jpg?v=1613530998" alt="" />
              <div className="card-text">
                <h5 className='bloger-name'>Jennifer Keyton</h5>
                <h6 className='bloger-position'>{aboutData.employee_1_postion}</h6>

                <div className="line"></div>

                <div className="social-media">
                  <i className="fa-brands fa-twitter"></i>
                  <i className="fa-brands fa-facebook-f"></i>
                  <i className="fa-brands fa-google-plus-g"></i>
                  <i className="fa-brands fa-linkedin-in"></i>
                </div>

              </div>
            </div>
          </div>

          <div className="col-12 col-sm-6 col-md-4 col-lg-3">
            <div className="bloger-card">
              <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/team-4_f50b5f75-592e-4358-a916-32a7a0b4740a_1024x1024.jpg?v=1613530998" alt="" />
              <div className="card-text">
                <h5 className='bloger-name'>MACK KEYTON</h5>
                <h6 className='bloger-position'>{aboutData.employee_2_postion}</h6>

                <div className="line"></div>

                <div className="social-media">
                  <i className="fa-brands fa-twitter"></i>
                  <i className="fa-brands fa-facebook-f"></i>
                  <i className="fa-brands fa-google-plus-g"></i>
                  <i className="fa-brands fa-linkedin-in"></i>
                </div>

              </div>
            </div>
          </div>

          <div className="col-12 col-sm-6 col-md-4 col-lg-3">
            <div className="bloger-card">
              <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/team-1_8b680953-c89e-4cb6-9fb3-91654a472325_1024x1024.jpg?v=1613530998" alt="" />
              <div className="card-text">
                <h5 className='bloger-name'>JASMINE WAN</h5>
                <h6 className='bloger-position'>{aboutData.employee_3_postion}</h6>

                <div className="line"></div>

                <div className="social-media">
                  <i className="fa-brands fa-twitter"></i>
                  <i className="fa-brands fa-facebook-f"></i>
                  <i className="fa-brands fa-google-plus-g"></i>
                  <i className="fa-brands fa-linkedin-in"></i>
                </div>

              </div>
            </div>
          </div>

          <div className="col-12 col-sm-6 col-md-4 col-lg-3">
            <div className="bloger-card">
              <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/team-3_1575e013-a12d-4a41-ba95-4b0559b899ec_1024x1024.jpg?v=1613530998" alt="" />
              <div className="card-text">
                <h5 className='bloger-name'>TAYLOR COPPER</h5>
                <h6 className='bloger-position'>{aboutData.employee_4_postion}</h6>

                <div className="line"></div>

                <div className="social-media">
                  <i className="fa-brands fa-twitter"></i>
                  <i className="fa-brands fa-facebook-f"></i>
                  <i className="fa-brands fa-google-plus-g"></i>
                  <i className="fa-brands fa-linkedin-in"></i>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
      {/* Blogs SECTION END */}

      <div className="post-main">
        <div className="container-fluid">

          <div className="row g-4">
            <div className="col-12 col-sm-12 col-md-6" id='post-left'>
              <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/img-1_1024x1024.png?v=1613530999" alt="" />
            </div>

            <div className="col-12 col-sm-12 col-md-6" id='post-right'>
              <div className="text-box">
                <h2>{aboutData.end_section_text_head}</h2>

                <p>
                  {aboutData.end_section_text_1}
                </p>

                <p>
                  {aboutData.end_section_text_2}
                </p>
              </div>
            </div>
          </div>

        </div>
      </div>

    </section>
  )
}

export default About