import React, { useState } from 'react'
import HeroPart from '../components/HeroPart'
import { useNavigate } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { addBlogToDatabase } from '../action/blogAction'
import Alert from '../components/Alert';

import { v4 as uuid } from 'uuid';

const AddBlog = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const [addBlogTitle, setAddBlogTitle] = useState("");
    const [addBlogImg, setAddBlogImg] = useState("");
    const [addBlogerName, setAddBlogerName] = useState("");
    const [addBlogDesc, setAddBlogDesc] = useState("");
    const [addComments, setAddComments] = useState("");
    const [addBlogDate, setAddBlogDate] = useState("");

    // Alert Setting
    const [alertText, setAlertText] = useState("");
    const [alertType, setAlertType] = useState("");
    //
    return (
        <section className='addBlogPage'>
            <HeroPart subHead="Add Blog" textHead="Blog" />

            <div className="edit-blog-main my-5">
                <div className="container">
                    <div className="edit-blog">
                        <div className="head">
                            <h3>Add Blog</h3>
                        </div>

                        <form onSubmit={(e) => {
                            e.preventDefault();
                            if (!addBlogDate || !addBlogImg || !addBlogTitle || !addBlogDesc || !addBlogerName || !addComments ) {
                                setAlertText("Please fill the gap");
                                setAlertType("warning");
                            }
                            else {

                                dispatch(addBlogToDatabase({ id: uuid(), title: addBlogTitle, desc: addBlogDesc, img: addBlogImg, comments: addComments, date: addBlogDate, bloger: addBlogerName }))
                                navigate('/dashboard')
                                window.location.reload();
                            }

                        }}>
                            <Alert alertText={alertText} alertType={alertType} />

                            <div className="inputs-main">
                                <div className="row">
                                    <div className="col-12 col-sm-12 col-md-6">
                                        <div className="col-inputs-main">

                                            <h5>Blog Image</h5>
                                            <input type="text" placeholder='Image' onChange={(e) => setAddBlogImg(e.target.value)} />

                                            <h5>Blog Title</h5>
                                            <input type="text" placeholder='Title' onChange={(e) => setAddBlogTitle(e.target.value)} />


                                            <h5>Bloger Name</h5>
                                            <input type="text" placeholder='Bloger' onChange={(e) => setAddBlogerName(e.target.value)} />

                                            <h5>Blog Date</h5>
                                            <input type="text" placeholder='Date' onChange={(e) => setAddBlogDate(e.target.value)} />

                                            <h5>Blog Comment Count</h5>
                                            <input type="text" placeholder='Comments' onChange={(e) => setAddComments(e.target.value)} />
                                        </div>

                                    </div>

                                    <div className="col-12 col-sm-12 col-md-6">
                                        <div className="col-inputs-main">
                                            <h5>Blog Description</h5>

                                            <textarea name="" placeholder="Description" cols="30" rows="10" id='text-area' onChange={(e) => setAddBlogDesc(e.target.value)}></textarea>

                                            <button type='Submit'>Submit</button>
                                        </div>

                                    </div>
                                </div>
                            </div>

                        </form>
                    </div>
                </div>

            </div>
        </section>
    )
}

export default AddBlog