import React, { useState } from 'react'
import testi_monials from '../data/Testi-Monial';
import { useEffect } from 'react';

const TestiMonialSlider = () => {
    const [current, setCurrent] = useState(0);
    const [sliderData, setSliderData]= useState(testi_monials);
    const currentLang = localStorage.getItem('lang');
    useEffect(() => {
        if (currentLang === "EN"){
            setSliderData(testi_monials[0]);
        }
        
        if (currentLang === "AZE"){
            setSliderData(testi_monials[1]);
        }
    },[currentLang])

    console.log(testi_monials[0]);
    return (
        <div className="testi-monial-slider">
            <p>{sliderData[current].article}</p>

            <div className="slider-bottom">
                <img src={sliderData[current].img} alt="" />

                <div className="info">
                    <p className='info-name'>{sliderData[current].name}</p>
                    <p className='info-position'>{sliderData[current].position}</p>
                </div>

                <button className='blue-bordered-btn' id='testi-monial-previous' onClick={
                    () => {
                        if (current > 0)
                            setCurrent(current - 1);
                        else
                            setCurrent(sliderData.length - 1)
                    }
                }>
                    <div className="top-frame"></div>
                    <i className="fa-solid fa-arrow-left"></i>
                    <div className="bottom-frame"></div>

                </button>

                <button className='blue-bordered-btn' id='testi-monial-next' onClick={
                    () => {
                        if (current + 1 < sliderData.length)
                            setCurrent(current + 1);
                        else
                            setCurrent(0)
                    }
                }>
                    <div className="top-frame"></div>
                    <i className="fa-solid fa-arrow-right"></i>
                    <div className="bottom-frame"></div>
                </button>

            </div>

            <div className="slider-indicator">
                {
                    sliderData.map((item, c) => {
                        if (c === current) {
                            return (
                                <div className="indicator-single active" onClick={() => {
                                    setCurrent(c)
                                }}></div>
                            )
                        } else {
                            return (
                                <div className="indicator-single" onClick={() => {
                                    setCurrent(c)
                                }}></div>
                            )
                        }
                    })
                }
            </div>
        </div>
    )
}

export default TestiMonialSlider