
import { useState,createContext, useEffect } from "react";
import laguage_data from "../data/LanguageData";

export const LanguageContext = createContext();

export const LanguageProvider =(props)=>{
    const [translatedData,setTranslatedData] = useState([]);
    useEffect(() => {
        const currentLang = localStorage.getItem("lang");
        if (currentLang === "EN") {
          setTranslatedData(laguage_data[0][0]);
        } else if (currentLang === "AZE") {
          setTranslatedData(laguage_data[1][0]);
        }
      },[])
      
  return  <LanguageContext.Provider value={[translatedData,setTranslatedData]}>
        {props.children}
    </LanguageContext.Provider>
}