import React from 'react'
import HeroPart from '../components/HeroPart'
import watches_data from '../data/WatchesData'
import blog_data from '../data/BlogData'
import { LinkContainer } from 'react-router-bootstrap'
const Blog = () => {
    return (
        <section className='blogPage'>
            <HeroPart subHead='News' textHead='News' />

            <div className="blogBody">
                <div className="container">
                    <div className="row g-3">
                        <div className="col-12 col-sm-12 col-md-3 col-lg-2" id='left-main'>


                            <div className="filter-item-main mt-5">
                                <div className="head-main">
                                    <h6>Recent Articles</h6>
                                </div>

                                {
                                    blog_data.slice(0, 3).map((item) => {
                                        return (
                                            <LinkContainer to={`/blog/${item.id}`}>
                                                <div className="list-item">
                                                    <div className="img-box">
                                                        <img src={item.img} alt="" />
                                                    </div>

                                                    <div className="text-box">
                                                        <h5 className='title'>{item.title}</h5>
                                                        <p className="date">{item.date}</p>
                                                    </div>
                                                </div>
                                            </LinkContainer>
                                        )
                                    })
                                }

                            </div>


                            {/* CATEGORY */}
                            <div className="filter-item-main mt-5">
                                <div className="head-main">
                                    <h6>Category</h6>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>Coat</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>Equipment</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>Fashion</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>Model</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>Sling Belt</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>Stylish</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>Trendy</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>Watch</p>
                                    </div>
                                </div>
                            </div>
                            {/* CATEGORY    END*/}


                            {/* BEST DEALS  */}
                            <div className="filter-item-main mt-5">
                                <div className="head-main">
                                    <h6>Best Deals</h6>
                                </div>

                                <div className="list-item" id='best-deals-list-item'>

                                    <div id="carouselExample" className="carousel slide">
                                        <div className="carousel-inner">
                                            <div className="carousel-item active">
                                                <div className="slider-main">
                                                    <div className="slider-img">
                                                        <LinkContainer to="/product/5">
                                                        <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/products/product19_f981339e-39ac-4562-9b99-b9435f46c935.jpg?v=1532930768" alt="" />
                                                        </LinkContainer>
                                                    </div>

                                                    <p className='watch-title'>Analog Water proof Watch</p>
                                                    <p className='watch-price'>$212.00</p>
                                                </div>
                                            </div>

                                            {
                                                watches_data.map((item) => {
                                                    return (
                                                        <div className="carousel-item ">
                                                            <div className="slider-main">
                                                                <div className="slider-img">
                                                                    <LinkContainer to={`/product/${item.id}`}>
                                                                    <img src={item.main_img} alt="" />
                                                                    </LinkContainer>
                                                                </div>

                                                                <p className='watch-title'>{item.title}</p>
                                                                <p className='watch-price'>${item.price}</p>
                                                            </div>
                                                        </div>
                                                    )
                                                })
                                            }


                                        </div>

                                        <button className="carousel-control-prev" type="button" data-bs-target="#carouselExample" data-bs-slide="prev">
                                            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                            <span className="visually-hidden">Previous</span>
                                        </button>

                                        <button className="carousel-control-next" type="button" data-bs-target="#carouselExample" data-bs-slide="next">
                                            <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                            <span className="visually-hidden">Next</span>
                                        </button>
                                    </div>

                                </div>

                            </div>
                            {/* BEST DEALS      END*/}
                        </div>

                        <div className="col-12 col-sm-12 col-md-9 col-lg-10" id='right-main'>
                            <div className="row g-5">

                                {
                                    blog_data.map((item) => {
                                        return (

                                            <div className="col-12 col-sm-6 col-md-6 col-lg-4">
                                                <div className="blog-card">

                                                    <LinkContainer to={`/blog/${item.id}`}>
                                                        <div className="img-container">
                                                            <img src={item.img} alt="" />

                                                            <div className="hover-div" id='hover-div-left'></div>
                                                            <div className="hover-div" id='hover-div-right'></div>

                                                        </div>
                                                    </LinkContainer>

                                                    <div className="blog-body">
                                                        <h4 className='blog-title'>{item.title}</h4>
                                                        <p className='blog-date'>{item.date}</p>
                                                        <p className='blog-rate'>
                                                            <span className='comment'>{item.comments} Comments</span>
                                                            |   <i className="fa-solid fa-user"></i>
                                                            <span>{item.bloger}</span>
                                                        </p>
                                                        <p className='blog-desc'> {item.desc.slice(0, 167)} ...</p>
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })
                                }




                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </section>
    )
}

export default Blog