import React from 'react'
import { LinkContainer } from 'react-router-bootstrap';
import { useCart } from 'react-use-cart'
import { useWishlist } from 'react-use-wishlist';

const ProductInfo = ({ main_img, second_img, prev_price, title, desc, price, currencyIcon, allData,id , heartColor, heartStyle }) => {
    const { addItem } = useCart();
    const {addWishlistItem,getWishlistItem,removeWishlistItem} = useWishlist();
    return (
        <div className='product-info'>
            <LinkContainer to={`/product/${id}`}>
                <div className="image-box">
                    <img className='main-img' src={main_img} alt="" />
                    <img className='second-img' src={second_img} alt="" />
                </div>
            </LinkContainer>

            <div className="info-box">
                <div className="text-main">
                    <p className='title'>{title}</p>
                    <p className='price'>{currencyIcon} {price} <del>{prev_price}</del></p>
                    <p className='desc'>{desc}</p>

                    <div className="buttons-div">

                        <button className='blue-bordered-btn' onClick={() => addItem(allData)}>
                            <div className="top-frame"></div>
                            <i className="fa-solid fa-cart-shopping"></i>
                            Add to Cart
                            <div className="bottom-frame"></div>

                        </button>

                        <button className='blue-bordered-btn' onClick={
                            ()=>{
                                if(getWishlistItem(id)!==undefined){
                                    removeWishlistItem(id);
                                }else{
                                    addWishlistItem(allData);
                                }
                            }
                            }>
                            <div className="top-frame"></div>
                            <i className= {`fa-${heartStyle} fa-heart text-${heartColor}`}></i>
                            Add to Wishlist
                            <div className="bottom-frame"></div>

                        </button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ProductInfo