import React from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import HeroPart from '../components/HeroPart'
import { removeBlog, removeBlogFromDatabase } from '../action/blogAction'
import { connect, useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
const Dashboard = () => {

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const blogValue = useSelector(state => state)  // We just use it instead of (connect)

  return (
    <section className='cartPage'>
      <HeroPart subHead="Dashboard" textHead="Admin Panel" />



      <div className="container-fluid">
        <LinkContainer to="/addBlog">
          <button className='blue-bordered-btn mt-5 mb-3'>
            <div className="top-frame"></div>
            Add Blog
            <div className="bottom-frame"></div>
          </button>
        </LinkContainer>

        <div className="table-body my-5">
          <div className="head">
            <div className="row">
              <div className="col-3 col-sm-3 col-md-4 col-lg-4 head-title">
                <h5 className='text-center text-white'>Blog</h5>
              </div>

              <div className="col-3 col-sm-3 col-md-2 col-lg-2 head-title">
                <h5 className='text-center text-white'>Bloger</h5>
              </div>


              <div className="col-3 col-sm-3 col-md-2 col-lg-2 head-title list-item-total">
                <h5 className='text-center text-white'>Description</h5>
              </div>

              <div className="col-3 col-sm-3 col-md-2 col-lg-2 head-title">
                <h5 className='text-center text-white'>Edit</h5>
              </div>

              <div className="col-3 col-sm-3 col-md-2 col-lg-2 head-title">
                <h5 className='text-center text-white'>Remove</h5>
              </div>
            </div>

            {/* PRODUCTS LIST STAR */}
            {
              blogValue.map((item) => {
                return (
                  <div className="row" key={item.id}>
                    <div className="col-3 col-sm-3 col-md-4 col-lg-4 list-item">
                        <div className="list-item-body">
                          <div className="img-main">
                            <img src={item.img} alt="" />
                          </div>
                          <span className='title'>{item.title}</span>
                        </div>
                    </div>

                    <div className="col-3 col-sm-3 col-md-2 col-lg-2 list-item">
                      <div className="list-item-body">
                        <span>{item.bloger}</span>
                      </div>
                    </div>


                    <div className="col-2 col-sm-3 col-md-2 col-lg-2 list-item list-item-total">
                      <div className="list-item-body">
                        <p className='blog-desc'>{item.desc.slice(0, 50)}...</p>
                      </div>
                    </div>


                    <div className="col-3 col-sm-3 col-md-2 col-lg-2 list-item">
                      <div className="list-item-body ">
                        <LinkContainer to={`/dashboard/edit/${item.id}`}>
                          <button className='editBtn'> Edit </button>
                        </LinkContainer>
                      </div>
                    </div>





                    <div className="col-2 col-sm-3 col-md-2 col-lg-2 list-item">
                      <div className="list-item-body">
                        <span>
                          <i className="fa-solid fa-trash" onClick={() =>{
                              dispatch(removeBlogFromDatabase(item.id));
                              window.location.reload();
                          } }></i >
                        </span>
                      </div>
                    </div>
                  </div>
                )
              })
            }

          </div>
        </div>


      </div>

    </section>
  )
}

export default Dashboard