import React, { useState } from 'react'
import blog_data from '../../data/BlogData'
import { useNavigate, useParams } from 'react-router-dom'
import HeroPart from '../../components/HeroPart';
import { connect, useDispatch, useSelector } from 'react-redux';
import { editBlog, editBlogFromDatabase } from '../../action/blogAction'
import Alert from '../../components/Alert';

const EditBlog = () => {
    // Alert Setting
    const [alertText, setAlertText] = useState("");
    const [alertType, setAlertType] = useState("");
    //

    const navigate = useNavigate();
    const { blogId } = useParams();

    const blogValue = useSelector(state => state);

    const currentBlog = blogValue.find(item => item.id == blogId);

    const [blogDesc, setBlogDesc] = useState(currentBlog.desc);
    const [blogImg, setBlogImg] = useState(currentBlog.img);
    const [blogDate, setBlogDate] = useState(currentBlog.date);
    const [blogTitle, setBlogTitle] = useState(currentBlog.title);
    const [blogerName, setBlogerName] = useState(currentBlog.bloger);
    const [blogerComments, setBlogComments] = useState(currentBlog.comments);

    const dispatch = useDispatch();
    const formSubmit = (e) => {
        e.preventDefault();
        if (!blogDesc || !blogDate || !blogTitle || !blogerName || !blogImg) {
            setAlertText("Please fill the gap");
            setAlertType("warning");
        } else {
            dispatch(editBlogFromDatabase(currentBlog.id, {
                title: blogTitle,
                desc: blogDesc,
                img: blogImg,
                comments: blogerComments,
                date: blogDate,
                bloger: blogerName,
            }));
            navigate('/dashboard');
            window.location.reload();
        }
    }
    return (
        <section className='editBlogPage'>

            <HeroPart subHead="Edit" textHead={currentBlog.title} />

            <div className="container">
                <div className="img-box">
                    <img src={currentBlog.img} alt="" />
                </div>

                <Alert alertText={alertText} alertType={alertType} />
                
                <div className="edit-blog-main">
                    <div className="container">
                        <div className="edit-blog">
                            <div className="head">
                                <h3>Edit Blog Details</h3>
                            </div>

                            <form onSubmit={formSubmit}>

                                <div className="inputs-main">
                                    <div className="row">
                                        <div className="col-12 col-sm-12 col-md-6">
                                            <div className="col-inputs-main">

                                                <h5>Blog Image</h5>
                                                <input type="text" placeholder='Image' value={blogImg} onChange={(e) => setBlogImg(e.target.value)} />

                                                <h5>Blog Title</h5>
                                                <input type="text" placeholder='Title' value={blogTitle} onChange={(e) => setBlogTitle(e.target.value)} />


                                                <h5>Bloger Name</h5>
                                                <input type="text" placeholder='Bloger' value={blogerName} onChange={(e) => setBlogerName(e.target.value)} />

                                                <h5>Blog Date</h5>
                                                <input type="text" placeholder='Date' value={blogDate} onChange={(e) => setBlogDate(e.target.value)} />

                                                <h5>Blog Comment Count</h5>
                                                <input type="text" placeholder='Comments' value={blogerComments} onChange={(e) => setBlogComments(e.target.value)} />

                                            </div>

                                        </div>

                                        <div className="col-12 col-sm-12 col-md-6">
                                            <div className="col-inputs-main">
                                                <h5>Blog Description</h5>

                                                <textarea name="" placeholder="Message" cols="30" rows="10" id='text-area' value={blogDesc}
                                                    onChange={(e) => setBlogDesc(e.target.value)}
                                                ></textarea>

                                                <button type='Submit'>Submit</button>
                                            </div>

                                        </div>
                                    </div>
                                </div>

                            </form>
                        </div>
                    </div>

                </div>
            </div>
        </section>
    )
}

export default EditBlog;