import React, { useContext, useEffect, useState } from 'react'
import HeroPart from '../components/HeroPart'
import SingleCard from '../components/SingleCard';
import watches_data from '../data/WatchesData';
import ProductInfo from '../components/ProductInfo';
import { useWishlist } from 'react-use-wishlist';
import { LanguageContext } from '../context/LaguageContext';

const Woman = () => {
   
    // Language Context
    const [languageData] = useContext(LanguageContext);
    //
    const [shopData, setShopData] = useState({});
    const [buttons, setButtons] = useState({});

    useEffect(() => {
        if (languageData.shopPage !== undefined) {
            setShopData(languageData.shopPage);
        }
        // Buttons
        if (languageData.buttons !== undefined) {
            setButtons(languageData.buttons);
        }
    },)


    const [arrowPosition, setArrowPosition] = useState("down");
    const [colorFilterDisplay, setColorFilterDisplay] = useState("d-block");

    const [sizeArrowPosition, setSizeArrowPosition] = useState("down");
    const [sizeFilterDisplay, setSizeFilterDisplay] = useState("d-block");

    const [priceArrowPosition, setPriceArrowPosition] = useState("down");
    const [priceDisplay, setPriceFilterDisplay] = useState("d-block");

    const [brandArrowPosition, setBrandArrowPosition] = useState("down");
    const [brandDisplay, setBrandFilterDisplay] = useState("d-block");

    const [listStyle, setListStyle] = useState("card");

    // States for filter functional Buttons 
    const [rangeInput, setRangeInput] = useState(1000);


    const [sizeFilterBtn, setSizeFilterBtn] = useState("");

    // wishlist

    const { getWishlistItem } = useWishlist();


    // Filteren Man Data
    const genderFilterData = watches_data.filter((item)=> item.category === 'Woman');

    return (
        <section className='shopPage'>

            <HeroPart textHead={shopData.page_woman_head_title} subHead={shopData.page_woman_head_subTitle} />


            <div className="shopBody">
                <div className="container">
                    <div className="row g-3">
                        <div className="col-12 col-sm-12 col-md-3 col-lg-2" id='left-main'>
                            <div className="filter-item-main">
                                <div className="head-main">
                                    <h6>{shopData.filter_category_head}</h6>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>{shopData.filter_category_item_1}</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>{shopData.filter_category_item_2}</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>{shopData.filter_category_item_3}</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>{shopData.filter_category_item_4}</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>{shopData.filter_category_item_5}</p>
                                    </div>
                                </div>

                                <div className="list-item">
                                    <div className="item-title">
                                        <i className="fa-solid fa-circle"></i>
                                        <p>{shopData.filter_category_item_6}</p>
                                    </div>
                                </div>


                            </div>

                            <div className="filter-item-main mt-5">
                                <div className="head-main">
                                    <h6 onClick={() => {
                                        if (arrowPosition === "down") {
                                            setArrowPosition("up");
                                            setColorFilterDisplay("d-none")
                                        } else {
                                            setArrowPosition("down");
                                            setColorFilterDisplay("d-block")
                                        }
                                    }}>
                                        <span><i className={`fa-solid fa-chevron-${arrowPosition}`}></i></span>
                                        {shopData.filter_color_head}
                                    </h6>
                                </div>

                                <div className={`items-main ${colorFilterDisplay}`}>

                                    <div className="list-item color-list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle red"></i>
                                            <p>{shopData.filter_color_1}</p>
                                        </div>
                                    </div>

                                    <div className="list-item color-list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle yellow"></i>
                                            <p>{shopData.filter_color_2}</p>
                                        </div>
                                    </div>

                                    <div className="list-item color-list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle almond"></i>
                                            <p>{shopData.filter_color_3}</p>
                                        </div>
                                    </div>

                                    <div className="list-item color-list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle white"></i>
                                            <p>{shopData.filter_color_4}</p>
                                        </div>
                                    </div>

                                    <div className="list-item color-list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle black"></i>
                                            <p>{shopData.filter_color_5}</p>
                                        </div>
                                    </div>

                                    <div className="list-item color-list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle silver"></i>
                                            <p>{shopData.filter_color_6}</p>
                                        </div>
                                    </div>

                                    <div className="list-item color-list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle brown"></i>
                                            <p>{shopData.filter_color_7}</p>
                                        </div>
                                    </div>

                                    <div className="list-item color-list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle gold"></i>
                                            <p>{shopData.filter_color_8}</p>
                                        </div>
                                    </div>

                                    <div className="list-item color-list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle blue"></i>
                                            <p>{shopData.filter_color_9}</p>
                                        </div>
                                    </div>

                                    <div className="list-item color-list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle purple"></i>
                                            <p>{shopData.filter_color_10}</p>
                                        </div>
                                    </div>


                                </div>
                            </div>

                            <div className="filter-item-main mt-5" >
                                <div className="head-main">
                                    <h6 onClick={() => {
                                        if (sizeArrowPosition === "down") {
                                            setSizeArrowPosition("up");
                                            setSizeFilterDisplay("d-none");
                                        } else {
                                            setSizeArrowPosition("down");
                                            setSizeFilterDisplay("d-block")
                                        }
                                    }}>
                                        <span><i className={`fa-solid fa-chevron-${sizeArrowPosition}`}></i></span>
                                        {shopData.filter_size_head}
                                        
                                    </h6>
                                </div>

                                <div className={`items-main ${sizeFilterDisplay}`}>

                                    <div className="size-items-main">
                                        {
                                            sizeFilterBtn === "S" ? <p className='active' onClick={() => setSizeFilterBtn("S")}>S</p> :
                                                <p onClick={() => setSizeFilterBtn("S")}>S</p>

                                        }
                                        {
                                            sizeFilterBtn === "M" ? <p className='active' onClick={() => setSizeFilterBtn("M")}>M</p> :
                                                <p onClick={() => setSizeFilterBtn("M")}>M</p>

                                        }
                                        {
                                            sizeFilterBtn === "L" ? <p className='active' onClick={() => setSizeFilterBtn("L")}>L</p> :
                                                <p onClick={() => setSizeFilterBtn("L")}>L</p>
                                        }

                                    </div>


                                </div>
                            </div>


                            <div className="filter-item-main mt-5">
                                <div className="head-main">
                                    <h6 onClick={() => {
                                        if (priceArrowPosition === "down") {
                                            setPriceArrowPosition("up");
                                            setPriceFilterDisplay("d-none");
                                        } else {
                                            setPriceArrowPosition("down");
                                            setPriceFilterDisplay("d-block");
                                        }
                                    }}>
                                        <span><i className={`fa-solid fa-chevron-${priceArrowPosition}`}></i></span>
                                        {shopData.filter_price_head}

                                    </h6>
                                </div>

                                <div className={`items-main ${priceDisplay}`}>

                                    <div className="list-item">

                                        <input type="range" name="" id="rangeSlider" min={0} max={1000} value={rangeInput}
                                            onInput={(e) => {
                                                setRangeInput(e.target.value);
                                            }} />
                                        <p className='text-white'>{rangeInput}</p>
                                    </div>


                                </div>

                            </div>

                            <div className="filter-item-main mt-5">
                                <div className="head-main">
                                    <h6 onClick={() => {
                                        if (brandArrowPosition === "down") {
                                            setBrandArrowPosition("up");
                                            setBrandFilterDisplay("d-none");
                                        } else {
                                            setBrandArrowPosition("down");
                                            setBrandFilterDisplay("d-block");
                                        }
                                    }}>
                                        <span><i className={`fa-solid fa-chevron-${brandArrowPosition}`}></i></span>
                                        {shopData.filter_brand_head}

                                    </h6>
                                </div>

                                <div className={`items-main ${brandDisplay}`}>

                                    <div className="list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle"></i>
                                            <p>Baggit</p>
                                        </div>
                                    </div>

                                    <div className="list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle"></i>
                                            <p>Fostelo</p>
                                        </div>
                                    </div>

                                    <div className="list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle"></i>
                                            <p>Bitwell</p>
                                        </div>
                                    </div>

                                    <div className="list-item">
                                        <div className="item-title">
                                            <i className="fa-solid fa-circle"></i>
                                            <p>Adraxx</p>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>

                        {/* Right Part Main Start */}
                        <div className="col-12 col-sm-12 col-md-9 col-lg-10" id='right-main'>
                            <div className="container">
                                <div className="right-head">

                                    <button className='blue-bordered-btn' onClick={() => { setListStyle("card") }}>
                                        <div className="top-frame"></div>
                                        <i className="fa-solid fa-table-cells-large"></i>
                                        <div className="bottom-frame"></div>

                                    </button>

                                    <button className='blue-bordered-btn' onClick={() => { setListStyle("list") }}>
                                        <div className="top-frame"></div>
                                        <i className="fa-solid fa-bars"></i>
                                        <div className="bottom-frame"></div>

                                    </button>


                                </div>

                                <div className="right-body">
                                    <div className="row g-3">
                                        {
                                            genderFilterData.filter((item) => item.price < rangeInput).map(function (item) {
                                                var heartColor = "";
                                                var heartStyle = "";

                                                if (getWishlistItem(item.id) != undefined) {
                                                    heartColor = 'danger';
                                                    heartStyle = 'solid';
                                                } else {
                                                    heartStyle = 'regular'
                                                }

                                                if (listStyle === "card") {
                                                    return (
                                                        <div className="col-12 col-sm-6 col-md-4 col-lg-3">

                                                            <SingleCard
                                                                key={item.id}
                                                                id={item.id}
                                                                main_img={item.main_img}
                                                                second_img={item.second_img}
                                                                price={item.price}
                                                                prev_price={item.prev_price}
                                                                title={item.title}
                                                                currencyIcon={"$"}
                                                                allData={item}
                                                                heartColor={heartColor}
                                                                heartStyle={heartStyle}
                                                            />
                                                        </div>
                                                    )
                                                } if (listStyle === "list") {
                                                    return (
                                                        <div className="col-12">
                                                            <ProductInfo
                                                                key={item.id}
                                                                id={item.id}
                                                                main_img={item.main_img}
                                                                second_img={item.second_img}
                                                                price={item.price}
                                                                prev_price={item.prev_price}
                                                                title={item.title}
                                                                desc={item.desc}
                                                                currencyIcon={"$"}
                                                                allData={item}
                                                                heartColor={heartColor}
                                                                heartStyle={heartStyle}
                                                            />
                                                        </div>
                                                    )
                                                }
                                            })
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Woman;