import React, { useContext, useEffect, useState } from 'react'
import HeroPart from '../components/HeroPart'
import { LanguageContext } from '../context/LaguageContext';

const Contact = () => {

    // Language Context
    const [languageData] = useContext(LanguageContext);

    const [contactData , setContactData] = useState({});


    useEffect(() => {
        if (languageData.contactPage !== undefined) {
          setContactData(languageData.contactPage);
        }
      },)


    return (
        <section className='aboutPage'>
            <HeroPart textHead={contactData.page_head_title} subHead={contactData.page_head_subTitle} />

            <div className="social-media-accounts">
                <div className="container">

                    <div className="row g-5">
                        <div className="col-12 col-sm-6 col-md-3">
                            <div className="account-item">
                                <div className="icon">
                                    <div className="inner-circle"></div>
                                    <i className="fa-solid fa-paper-plane"></i>
                                </div>

                                <p className='detail-text'>{contactData.contact_item_1}</p>
                                <p className='detail-sub-title'>{contactData.contact_item_1_text}</p>
                            </div>
                        </div>

                        <div className="col-12 col-sm-6 col-md-3">
                            <div className="account-item">
                                <div className="icon">
                                    <div className="inner-circle"></div>
                                    <i className="fa-regular fa-envelope"></i>
                                </div>

                                <p className='detail-text'>{contactData.contact_item_2}</p>
                                <p className='detail-sub-title'>{contactData.contact_item_2_text}</p>
                            </div>
                        </div>

                        <div className="col-12 col-sm-6 col-md-3">
                            <div className="account-item">
                                <div className="icon">
                                    <div className="inner-circle"></div>
                                    <i className="fa-solid fa-phone"></i>
                                </div>

                                <p className='detail-text'>{contactData.contact_item_3}</p>
                                <p className='detail-sub-title'>{contactData.contact_item_3_text}</p>
                            </div>
                        </div>

                        <div className="col-12 col-sm-6 col-md-3">
                            <div className="account-item">
                                <div className="icon">
                                    <div className="inner-circle"></div>
                                    <i className="fa-regular fa-clock"></i>
                                </div>

                                <p className='detail-text'>{contactData.contact_item_4}</p>
                                <p className='detail-sub-title'>{contactData.contact_item_4_text}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* MAP SECTION START */}
            <div className="map-container">
                <div className="container">
                    <iframe
                        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3039.4761858647776!2d49.826747574859404!3d40.37613765811076!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x40307d4ec5fe0f83%3A0x805256f8d1c1ec75!2sMatrix%20Training%20Center!5e0!3m2!1str!2saz!4v1684232424622!5m2!1str!2saz"
                        width={600}
                        height={450}
                        style={{ border: 0 }}
                        allowFullScreen=""
                        loading="lazy"
                        referrerPolicy="no-referrer-when-downgrade"
                    />
                </div>
            </div>
            {/* MAP SECTION END */}

            <div className="send-us-message">
                <div className="container">
                    <div className="send-us-main">
                        <div className="head">
                            <h3>{contactData.form_head_text}</h3>
                        </div>

                        <div className="inputs-main">
                            <div className="row">
                                <div className="col-12 col-sm-12 col-md-6">
                                    <div className="col-inputs-main">

                                        <input type="text" placeholder={contactData.form_input_name} />
                                        <input type="text" placeholder={contactData.form_input_email} />
                                        <input type="text" placeholder={contactData.form_input_phone} />

                                    </div>

                                </div>

                                <div className="col-12 col-sm-12 col-md-6">
                                    <div className="col-inputs-main">

                                        <textarea name="" placeholder={contactData.form_input_message} cols="30" rows="10" id='text-area'></textarea>

                                        <button>Send</button>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>

            <div className="bottom-inputs-line container">
                <div className="line"></div>
            </div>

            <div className="social-media-main">
                <div className="icons-main">
                    <div className="icon">
                        <div className="inner-circle"></div>
                        <i class="fa-brands fa-facebook-f"></i>
                    </div>

                    <div className="icon">
                        <div className="inner-circle"></div>
                        <i class="fa-brands fa-twitter"></i>
                    </div>

                    <div className="icon">
                        <div className="inner-circle"></div>
                        <i class="fa-brands fa-google-plus-g"></i>
                    </div>

                    <div className="icon">
                        <div className="inner-circle"></div>
                        <i class="fa-brands fa-youtube"></i>
                    </div>

                    <div className="icon">
                        <div className="inner-circle"></div>
                        <i class="fa-brands fa-pinterest"></i>
                    </div>
                </div>
            </div>

        </section>
    )
}

export default Contact