import React, { useContext, useEffect, useState } from 'react'
import HeroPart from '../components/HeroPart'
import { useWishlist } from 'react-use-wishlist'
import { LinkContainer } from 'react-router-bootstrap';
import { LanguageContext } from '../context/LaguageContext';
import { useCart } from 'react-use-cart';

const WishList = () => {
    const { items, removeWishlistItem, getWishlistItem } = useWishlist();

    const { addItem } = useCart();

    // Language Context
    const [languageData] = useContext(LanguageContext);

    const [cartLanguage, setCartLanguage] = useState({});

    useEffect(() => {
        if (languageData.cartPage !== undefined) {
            setCartLanguage(languageData.cartPage);
        }
    },)


    return (
        <section className="wishlistPage">
            <HeroPart subHead={cartLanguage.wishlist_page_head_subTitle} textHead={cartLanguage.wishlist_page_head_title} />


            <div className="container">

                <div className="table-body my-5" id='wishlist'>
                    <div className="head">
                        <div className="row" id='wishlistRow'>
                            <div className="col-8 head-title">
                                <h5 className='text-center text-white'>{cartLanguage.list_product}</h5>
                            </div>




                            <div className="col-2 head-title">
                                <h5 className='text-center text-white'>{cartLanguage.list_remove}</h5>
                            </div>

                            <div className="col-2 head-title">
                                <h5 className='text-center text-white'>{cartLanguage.list_addToCart}</h5>
                            </div>

                        </div>

                        {/* PRODUCTS LIST STAR */}
                        {
                            items.map((item, c) => {
                                // if (c % 2 !== 0) {

                                return (
                                    <div className="row">
                                        <div className="col-8 list-item">
                                            <LinkContainer to={`/product/${item.id}`}>
                                                <div className="list-item-body">
                                                    <div className="img-main">
                                                        <img src={item.main_img} alt="" />
                                                    </div>
                                                    <span className='title'>{item.title}</span>
                                                </div>
                                            </LinkContainer>
                                        </div>



                                        <div className="col-2 col-sm-3 col-md-2 col-lg-2 list-item">
                                            <div className="list-item-body">
                                                <span>
                                                    <i className="fa-solid fa-trash" onClick={() => removeWishlistItem(item.id)}>

                                                    </i >
                                                </span>
                                            </div>
                                        </div>

                                        <div className="col-2 col-sm-3 col-md-2 col-lg-2 list-item">
                                            <div className="list-item-body">
                                                <span>
                                                    <i className="fa-solid fa-cart-shopping" onClick={() => {
                                                        addItem(item);
                                                    }}></i>
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                )
                                // }
                            })
                        }

                    </div>
                </div>

            </div>
        </section>
    )
}

export default WishList