import React from 'react'
import { useParams } from 'react-router-dom'
import blog_data from '../data/BlogData';
import HeroPart from '../components/HeroPart';

const BlogDetail = () => {
    const { id } = useParams();

    const blogDetail = blog_data.find((item) => item.id.toString() === id)
    return (
        <section className='blogDetailPage'>
            <div className="container">

                <HeroPart subHead="News" textHead={blogDetail.title} />

                <div className="img-box">
                    <img src={blogDetail.img} alt="" />
                </div>

                <div className="text-center">
                    <p>{blogDetail.date}</p>
                    <p className='blog-title'>{blogDetail.title}</p>
                    <p>{blogDetail.desc}</p>
                </div>


                
            <div className="leave-comment">
                <div className="container">
                    <div className="send-us-main">
                        <div className="head">
                            <h3>Send us message</h3>
                        </div>

                        <div className="inputs-main">
                            <div className="row">
                                <div className="col-12 col-sm-12 col-md-6">
                                    <div className="col-inputs-main">

                                        <input type="text" placeholder='Name' />
                                        <input type="text" placeholder='Email' />

                                    </div>

                                </div>

                                <div className="col-12 col-sm-12 col-md-6">
                                    <div className="col-inputs-main">

                                        <textarea name="" placeholder="Message" cols="30" rows="10" id='text-area'></textarea>

                                        <button>Send</button>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>

            </div>
        </section>

    )
}

export default BlogDetail