import React, { useContext, useEffect, useState } from 'react'
import HeroPart from '../components/HeroPart'
import { useCart } from 'react-use-cart';
import { LinkContainer } from 'react-router-bootstrap';
import { LanguageContext } from '../context/LaguageContext';
import Alert from '../components/Alert';

const Cart = () => {

  const { items, updateItemQuantity, removeItem, cartTotal, isEmpty } = useCart();

  // Alert Setting
  const [alertText, setAlertText] = useState("");
  const [alertType, setAlertType] = useState("");
  //
  
  // Language Context
  const [languageData] = useContext(LanguageContext);

  const [cartLanguage , setCartLanguage] = useState({});
  const [buttons,setButtons] = useState({});

    useEffect(() => {
        if (languageData.cartPage !== undefined) {
          setCartLanguage(languageData.cartPage);
        }
        //
        if(languageData.buttons !== undefined){
          setButtons(languageData.buttons);
        }
      },)


  return (
    <section className='cartPage'>
      <HeroPart subHead={cartLanguage.page_head_subTitle} textHead={cartLanguage.page_head_title} />

      <div className="container-fluid">

        <div className="table-body my-5">
          <div className="head">
            <div className="row">
              <div className="col-3 col-sm-3 col-md-4 col-lg-4 head-title">
                <h5 className='text-center text-white'>{cartLanguage.list_product}</h5>
              </div>

              <div className="col-3 col-sm-3 col-md-2 col-lg-2 head-title">
                <h5 className='text-center text-white'>{cartLanguage.list_price}</h5>
              </div>


              <div className="col-3 col-sm-3 col-md-2 col-lg-2 head-title">
                <h5 className='text-center text-white'>{cartLanguage.list_quantity}</h5>
              </div>


              <div className="col-3 col-sm-3 col-md-2 col-lg-2 head-title list-item-total">
                <h5 className='text-center text-white'>{cartLanguage.list_total}</h5>
              </div>


              <div className="col-3 col-sm-3 col-md-2 col-lg-2 head-title">
                <h5 className='text-center text-white'>{cartLanguage.list_remove}</h5>
              </div>
            </div>

            {/* PRODUCTS LIST STAR */}
            {
              items.map((item) => {
                return (
                  <div className="row" key={item.id}>
                    <div className="col-3 col-sm-3 col-md-4 col-lg-4 list-item">
                      <LinkContainer to={`/product/${item.id}`}>
                        <div className="list-item-body">
                          <div className="img-main">
                            <img src={item.main_img} alt="" />
                          </div>
                          <span className='title'>{item.title}</span>
                        </div>
                      </LinkContainer>
                    </div>

                    <div className="col-3 col-sm-3 col-md-2 col-lg-2 list-item">
                      <div className="list-item-body">
                        <span>${item.price}</span>
                      </div>
                    </div>


                    <div className="col-3 col-sm-3 col-md-2 col-lg-2 list-item">
                      <div className="list-item-body">
                        <div className="quantity-main">
                          <button className='quantity-button' onClick={() => updateItemQuantity(item.id, item.quantity - 1)} >
                            -
                          </button>

                          <span>{item.quantity}</span>

                          <button className='quantity-button' onClick={() => updateItemQuantity(item.id, item.quantity + 1)}>
                            +
                          </button>
                        </div>
                      </div>
                    </div>


                    <div className="col-2 col-sm-3 col-md-2 col-lg-2 list-item list-item-total">
                      <div className="list-item-body">
                        <span>${item.itemTotal}</span>
                      </div>
                    </div>


                    <div className="col-2 col-sm-3 col-md-2 col-lg-2 list-item">
                      <div className="list-item-body">
                        <span>
                          <i className="fa-solid fa-trash" onClick={() => removeItem(item.id)}>

                          </i >
                        </span>
                      </div>
                    </div>
                  </div>
                )
              })
            }

          </div>
        </div>


        <div className="total-price">
          <h3>{cartLanguage.list_total}: <span>${cartTotal}</span></h3>
        </div>
        
        <Alert alertText={alertText} alertType={alertType} />

        <div className="text-center my-5">
          <button className='blue-bordered-btn g-5' onClick={() => {
            if (isEmpty) {
              setAlertText("You don't have any item in cart!!!");
              setAlertType("warning");
            } else {
              setAlertText("Your order has been completed successfully!!!")
              setAlertType("success")
              items.map((item) => {
                removeItem((item.id));
              })
            }
          }}>
            <div className="top-frame"></div>
              {buttons.buy_now}
            <div className="bottom-frame"></div>

          </button>
        </div>
      </div>

    </section>
  )
}

export default Cart