import React, { useContext, useEffect, useState } from 'react'
import Slider1 from '../components/Slider1'
import TestiMonialSlider from '../components/TestiMonialSlider'
import SingleCard from '../components/SingleCard'
import watches_data from '../data/WatchesData'
import blog_data from '../data/BlogData'
import { LinkContainer } from 'react-router-bootstrap'
import { useCart } from 'react-use-cart'
import { LanguageContext } from '../context/LaguageContext'
import { useWishlist } from 'react-use-wishlist'

const Home = () => {
  const { addItem } = useCart();
  const { getWishlistItem, addWishlistItem, removeWishlistItem } = useWishlist();

  // Language Context
  const [languageData] = useContext(LanguageContext);

  // Divided Datas for Section (It is written like that for preventing undefined datas)
  const [homeData, sethomeData] = useState(languageData);
  const [postSection, setPostSection] = useState({});
  const [offerSection, setOfferSection] = useState({});
  const [goodWatchedAndSpecialMenu, setGoodWatchedAndSpecialMenu] = useState({});
  const [testMonials, setTestMonials] = useState({});
  const [hoWeAreSection, setHoWeAreSection] = useState({});
  const [journalSection, setJournalSection] = useState({});
  const [buttons, setButtons] = useState({});
  useEffect(() => {
    // Buttons
    if (languageData.buttons !== undefined) {
      setButtons(languageData.buttons);
    }
    if (languageData.homePage !== undefined) {
      sethomeData(languageData.homePage)
    }
    // PosterMain Section data
    if (homeData.postersMain !== undefined) {
      setPostSection(homeData.postersMain);
    }
    // DeliciousOffer and New Arrivals
    if (homeData.deliciousOffer !== undefined) {
      setOfferSection(homeData.deliciousOffer);
    }
    // Our good Watches and Special Menu
    if (homeData.goodWatchedAndSpecialMenu !== undefined) {
      setGoodWatchedAndSpecialMenu(homeData.goodWatchedAndSpecialMenu);
    }
    // Test Monials
    if (homeData.testMonials !== undefined) {
      setTestMonials(homeData.testMonials);
    }
    // Ho we are and Discount Section 
    if (homeData.hoWeAreSection !== undefined) {
      setHoWeAreSection(homeData.hoWeAreSection);
    }
    // Journal Section
    if (homeData.journalSection !== undefined) {
      setJournalSection(homeData.journalSection)
    }
  },)


  return (
    <section className='homePage '>
      <Slider1 />

      <div className="posters-main">
        <div className="row">
          <div className="col-12 col-sm-12 col-md-4 post-item" id='left-main'>
            <div className="post-main" data-aos="fade-up" data-aos-duration="3000">
              <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/img-1_c374460b-2608-4d16-a4ca-813bc1bd6908_1000X.jpg?v=1613530995" alt="" />
              <div className="text-overlay">
                <div className="text-box">
                  <p>{postSection.post_1_subTitle}</p>
                  <h6>{postSection.post_1_title}</h6>

                  <div className="btn-box">
                    <LinkContainer to="/shop">
                      <button className='blue-bordered-btn'>
                        <div className="top-frame"></div>
                        {buttons.view_all}
                        <div className="bottom-frame"></div>
                      </button>
                    </LinkContainer>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="col-12 col-sm-12 col-md-8 post-item" id='right-main'>
            <div className="row">

              <div className="col-12 col-sm-12 col-md-7 post-item">
                <div className="post-main" data-aos="zoom-out-left">
                  <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/img-5_02a27e38-f928-420e-adca-7abfdf31a781_1920X.jpg?v=1613530995" alt="" />
                  <div className="text-overlay">
                    <div className="text-box">
                      <p>{postSection.post_2_subTitle}</p>
                      <h6>{postSection.post_2_title}</h6>

                      <div className="btn-box">
                        <LinkContainer to="/shop">
                          <button className='blue-bordered-btn'>
                            <div className="top-frame"></div>
                            {buttons.view_all}
                            <div className="bottom-frame"></div>
                          </button>
                        </LinkContainer>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col-12 col-sm-12 col-md-5 post-item">
                <div className="post-main" data-aos="fade-up" data-aos-duration="2500">
                  <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/img-3-1_1920X.jpg?v=1613530995" alt="" />
                  <div className="text-overlay">
                    <div className="text-box">
                      <p>{postSection.post_3_subTitle}</p>
                      <h6>{postSection.post_3_title}</h6>

                      <div className="btn-box">
                        <LinkContainer to="/shop">
                          <button className='blue-bordered-btn'>
                            <div className="top-frame"></div>
                            {buttons.view_all}
                            <div className="bottom-frame"></div>
                          </button>
                        </LinkContainer>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col-12 col-sm-12 col-md-5 post-item">
                <div className="post-main" data-aos="fade-up" data-aos-duration="2000">
                  <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/img-2_80aec88a-806c-4683-9085-4d9185a23b48_1920X.jpg?v=1613530995" alt="" />
                  <div className="text-overlay">
                    <div className="text-box">
                      <p>{postSection.post_4_subTitle}</p>
                      <h6>{postSection.post_4_title}</h6>

                      <div className="btn-box">
                        <LinkContainer to="/shop">
                          <button className='blue-bordered-btn'>
                            <div className="top-frame"></div>
                            {buttons.view_all}
                            <div className="bottom-frame"></div>
                          </button>
                        </LinkContainer>
                      </div>
                    </div>
                  </div>
                </div>
              </div>


              <div className="col-12 col-sm-12 col-md-7 post-item">
                <div className="post-main" data-aos="fade-up" data-aos-duration="3000">
                  <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/img-4_a2e8a6f6-434c-4afe-a4cd-5f4bba6120f4_1920X.jpg?v=1613530995" alt="" />
                  <div className="text-overlay">
                    <div className="text-box">
                      <p>{postSection.post_5_subTitle}</p>
                      <h6>{postSection.post_5_title}</h6>

                      <div className="btn-box">
                        <LinkContainer to="/shop">
                          <button className='blue-bordered-btn'>
                            <div className="top-frame"></div>
                            {buttons.view_all}
                            <div className="bottom-frame"></div>
                          </button>
                        </LinkContainer>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

            </div>
          </div>
        </div>
      </div>

      <div className="delicious-offer">
        <div className="head"
          data-aos="fade-down"
          data-aos-easing="linear"
          data-aos-duration="1500">
          <div className="text-box">
            <h2>{offerSection.head}</h2>
            <p>{offerSection.text}</p>
          </div>
        </div>

        <div className="watches"
          data-aos="fade-up"
          data-aos-duration="2500">
          <div className="container-fluid">
            <div className="row g-5">
              {/* LOGO --1 */}
              <div className="col-12 col-sm-6 col-md-3">
                <div className="watch-logo">
                  <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/icon-4_medium.png?v=1613530993" alt="" />
                  <p className='watch-type'>{offerSection.watch_1_title}</p>

                  <div className="line">
                    <div className="square" id='square1'></div>
                    <div className="square" id='square2'></div>
                  </div>

                  <p className='text-long'>{offerSection.watch_1_text}</p>
                </div>
              </div>

              {/* LOGO --2 */}
              <div className="col-12 col-sm-6 col-md-3">
                <div className="watch-logo">
                  <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/icon-6_medium.png?v=1613530995" alt="" />
                  <p className='watch-type'>{offerSection.watch_2_title}</p>

                  <div className="line">
                    <div className="square" id='square1'></div>
                    <div className="square" id='square2'></div>
                  </div>

                  <p className='text-long'>{offerSection.watch_2_text}</p>
                </div>
              </div>

              {/* LOGO --3 */}
              <div className="col-12 col-sm-6 col-md-3">
                <div className="watch-logo">
                  <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/icon-4_medium.png?v=1613530993" alt="" />
                  <p className='watch-type'>{offerSection.watch_3_title}</p>

                  <div className="line">
                    <div className="square" id='square1'></div>
                    <div className="square" id='square2'></div>
                  </div>

                  <p className='text-long'>{offerSection.watch_3_text}</p>
                </div>
              </div>

              {/* LOGO --4 */}
              <div className="col-12 col-sm-6 col-md-3">
                <div className="watch-logo">
                  <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/icon-5_medium.png?v=1613530995" alt="" />
                  <p className='watch-type'>{offerSection.watch_4_title}</p>

                  <div className="line">
                    <div className="square" id='square1'></div>
                    <div className="square" id='square2'></div>
                  </div>

                  <p className='text-long'>{offerSection.watch_4_text}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* NEW ARRIVALS _START */}
      <div className="new-arrivals">
        <div className="head">
          <h2>{offerSection.newArrivals_head}</h2>
          <p>{offerSection.newArrivals_text}</p>
        </div>

        <div className="container">

          <div className="products">
            <div className="row g-5">
              {
                watches_data.slice(0, 4).map((item) => {

                  var heartColor = "";
                  var heartStyle = "";

                  if (getWishlistItem(item.id) != undefined) {
                    heartColor = 'danger';
                    heartStyle = 'solid';
                  } else {
                    heartStyle = 'regular'
                  }

                  return (
                    <div className="col-12 col-sm-12 col-md-6 col-lg-3">
                      <SingleCard
                        key={item.id}
                        id={item.id}
                        main_img={item.main_img}
                        second_img={item.second_img}
                        price={item.price}
                        prev_price={item.prev_price}
                        title={item.title}
                        currencyIcon={"$"}
                        allData={item}
                        heartColor={heartColor}
                        heartStyle={heartStyle}
                      />
                    </div>

                  )
                })
              }
            </div>
          </div>
        </div>
      </div>

      <div className="good-watches">
        <div className="watch-container">
          <div className="row">
            <div className="col-12 col-sm-6 col-md-6" id='left-text'>
              <div className="text-main">
                <h2>{goodWatchedAndSpecialMenu.good_watches_title}</h2>
                <p>{goodWatchedAndSpecialMenu.good_watches_text}</p>

                <LinkContainer to="/shop">
                  <button className='blue-bordered-btn'>
                    <div className="top-frame"></div>
                      {buttons.view_all}
                    <div className="bottom-frame"></div>

                  </button>
                </LinkContainer>

              </div>
            </div>

            <div className="col-12 col-sm-6 col-md-6 p-0">
              <div className="right-main">
                <div className="img-box">
                  <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/img-6_1920X.jpg?v=1613530994" alt="" />
                  <div className="hover-layer"></div>

                </div>

              </div>
            </div>
          </div>
        </div>
      </div>



      {/* Special Menu Start */}

      <div className="special-menu">
        <div className="head-container">
          <div className="head">
            <h2>{goodWatchedAndSpecialMenu.special_menu_head}</h2>
            <p>{goodWatchedAndSpecialMenu.special_menu_text}</p>
          </div>
        </div>

        <div className="products container">
          <div className="row g-4">
            {
              watches_data.slice(0, 6).map((item) => {

                return (
                  <div className="col-12 col-sm-12 col-md-6">

                    <div className="item container">

                      <LinkContainer to={`/product/${item.id}`}>
                        <div className="img-box">
                          <img src={item.main_img} alt="" />
                        </div>
                      </LinkContainer>

                      <div className="text-box">

                        <div className="text">
                          <p className='title'>{item.title}</p>
                          <p className='desc'>{item.desc.slice(0, 30)}...</p>
                          <p className='price'>${item.price}</p>
                        </div>

                        <button className='blue-bordered-btn' onClick={() => addItem(item)}>
                          <div className="top-frame"></div>
                          <i className="fa-solid fa-cart-shopping"></i>
                          <div className="bottom-frame"></div>

                        </button>
                      </div>
                    </div>
                  </div>

                )
              })
            }
          </div>
        </div>
      </div>

      {/* Testi-Monial Start */}
      <div className="testi-monials py-5">
        <div className="head">
          <h2>{testMonials.head}</h2>
        </div>
        <div className="main">
          <TestiMonialSlider />
        </div>
      </div>


      {/* About-Market Section __START__ */}
      <div className="about-market">
        <div className="container">
          <div className="row">
            <div className="col-12 col-sm-12 col-md-6">
              <div className="left-text container">

                <div className="head">
                  <h4>{hoWeAreSection.sub_head}</h4>
                  <h3>{hoWeAreSection.head}</h3>
                </div>

                <p className='long-text'>{hoWeAreSection.text_long}</p>


                <LinkContainer to="/shop">
                  <button className='blue-bordered-btn'>
                    <div className="top-frame"></div>
                      {buttons.view_all_collection}
                    <div className="bottom-frame"></div>

                  </button>
                </LinkContainer>
              </div>
            </div>

            <div className="col-12 col-sm-12 col-md-6">
              <div className="right-main">
                <div className="row g-2">
                  <div className="col-12 col-sm-6 col-md-6">
                    <div className="dial-item">
                      <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/icon-14_100x.png?v=1613530998" alt="" />
                      <p className='text-title'>{hoWeAreSection.watch_1_type}</p>

                      <div className="line">
                        <div className="square"></div>
                      </div>

                      <p className='text-long'>{hoWeAreSection.watch_1_text}</p>
                    </div>
                  </div>

                  <div className="col-12 col-sm-6 col-md-6">
                    <div className="dial-item">
                      <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/icon-12_100x.png?v=1613530998" alt="" />
                      <p className='text-title'>{hoWeAreSection.watch_2_type}</p>

                      <div className="line">
                        <div className="square"></div>
                      </div>

                      <p className='text-long'>{hoWeAreSection.watch_2_text}</p>
                    </div>
                  </div>

                  <div className="col-12 col-sm-6 col-md-6">
                    <div className="dial-item">
                      <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/icon-11_100x.png?v=1613530998" alt="" />
                      <p className='text-title'>{hoWeAreSection.watch_3_type}</p>

                      <div className="line">
                        <div className="square"></div>
                      </div>

                      <p className='text-long'>{hoWeAreSection.watch_3_text}</p>
                    </div>
                  </div>

                  <div className="col-12 col-sm-6 col-md-6">
                    <div className="dial-item">
                      <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/icon-13_100x.png?v=1613530998" alt="" />
                      <p className='text-title'>{hoWeAreSection.watch_4_type}</p>

                      <div className="line">
                        <div className="square"></div>
                      </div>

                      <p className='text-long'>{hoWeAreSection.watch_4_text}</p>
                    </div>
                  </div>


                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Discount All Section __START__ */}
      <div className="discount-all">
        <div className="text-frame">
          <div className="text-main">
            <h1>{hoWeAreSection.discount_section_head}</h1>
            <h3>{hoWeAreSection.discount_section_subTitle}</h3>

            <p>{hoWeAreSection.discount_section_text}</p>

            <LinkContainer to="/shop">
              <button className='blue-bordered-btn'>
                <div className="top-frame"></div>
                {buttons.view_more}

                <div className="bottom-frame"></div>

              </button>
            </LinkContainer>

          </div>
        </div>
      </div>

      {/* Journall Section __START__ */}
      <div className="journal-main">
        <div className="head">
          <h2>{journalSection.journalSection_head}</h2>
          <p>{journalSection.journalSection_subHead}</p>
        </div>

        <div className="blogs-main">
          <div className="row g-5">
            {
              blog_data.slice(0, 3).map((item) => {
                return (

                  <div className="col-12 col-sm-6 col-md-6 col-lg-4">
                    <div className="blog-card">

                      <LinkContainer to={`/blog/${item.id}`}>
                        <div className="img-container">
                          <img src={item.img} alt="" />

                          <div className="hover-div" id='hover-div-left'></div>
                          <div className="hover-div" id='hover-div-right'></div>

                        </div>
                      </LinkContainer>

                      <div className="blog-body">
                        <h4 className='blog-title'>{item.title}</h4>
                        <p className='blog-date'>{item.date}</p>
                        <p className='blog-rate'>
                          <span className='comment'>{item.comments} Comments</span>
                          |   <i className="fa-solid fa-user"></i>
                          <span>{item.bloger}</span>
                        </p>
                        <p className='blog-desc'> {item.desc.slice(0, 167)} ...</p>
                      </div>
                    </div>
                  </div>
                )
              })
            }


          </div>

          <div className="btn-part">
            <LinkContainer to="/blog">
              <button className='blue-bordered-btn'>
                <div className="top-frame"></div>
                {buttons.view_all_post}
                <div className="bottom-frame"></div>
              </button>
            </LinkContainer>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Home