import React, { useContext, useEffect, useState } from 'react'
import AOS from 'aos';
import '../../node_modules/aos/dist/aos.css'
import { LinkContainer } from 'react-router-bootstrap';
import { LanguageContext } from '../context/LaguageContext';
const Slider1 = () => {

    // Language Context
    const [languageData] = useContext(LanguageContext);
    const [sliderData, setSliderData] = useState(languageData);
    const [buttons,setButtons] = useState({});

    useEffect(() => {
        AOS.init();
        if (languageData.slider !== undefined) {
            setSliderData(languageData.slider)
        }
        // Buttons
        if (languageData.buttons !== undefined) {
            setButtons(languageData.buttons);
        }
    },)


    return (
        <div id="carouselExampleFade" className="carousel slide carousel-fade" data-bs-ride="carousel">
            <div className="carousel-inner">
                <div className="carousel-item active">

                    <div className="sliderItem" id="slider1">
                        <div className="frame">
                            <h2 className='head-text' data-aos="zoom-in"> </h2>
                            <div data-aos="fade-up" data-aos-duration="1000">
                                <h2 className='head-text' data-aos="zoom-in" >{sliderData.slider_1_title}</h2>
                                <h5 className='discount-text'>{sliderData.slider_1_subTitle}</h5>
                                <h5 className='sub-head'>{sliderData.slider_1_sub_text}</h5>
                                <h4 className='discount-code'>{sliderData.slider_1_code}</h4>
                                <LinkContainer to="/shop">
                                    <button className='shop-button'>{buttons.shop_now}</button>
                                </LinkContainer>
                            </div>
                        </div>

                    </div>

                </div>



                <div className="carousel-item">
                    <div className="sliderItem" id="slider2">
                        <div className="frame">
                            <h2 className='head-text' data-aos="zoom-in" >{sliderData.slider_2_title}</h2>
                            <h5 className='discount-text'>{sliderData.slider_2_subTitle}</h5>
                            <h5 className='sub-head'>{sliderData.slider_2_sub_text}</h5>
                            <h4 className='discount-code'>{sliderData.slider_2_code}</h4>
                            <LinkContainer to="/shop">
                                <button className='shop-button'>{buttons.shop_now}</button>
                            </LinkContainer>
                        </div>

                    </div>

                </div>


                <div className="carousel-item">

                    <div className="sliderItem" id="slider3">
                        <div className="frame">
                            <h2 className='head-text' data-aos="zoom-in" >{sliderData.slider_3_title}</h2>
                            <h5 className='discount-text'>{sliderData.slider_3_subTitle}</h5>
                            <h5 className='sub-head'>{sliderData.slider_3_sub_text}</h5>
                            <h4 className='discount-code'>{sliderData.slider_3_code}</h4>
                            <LinkContainer to="/shop">
                                <button className='shop-button'>{buttons.shop_now}</button>
                            </LinkContainer>
                        </div>

                    </div>

                </div>
            </div>


            <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleFade" data-bs-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="visually-hidden">Previous</span>
            </button>
            <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleFade" data-bs-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="visually-hidden">Next</span>
            </button>
        </div>
    )
}

export default Slider1