import React from 'react'
import { useState } from 'react';
import { LinkContainer } from 'react-router-bootstrap';
import { useCart } from 'react-use-cart';
import { useWishlist } from 'react-use-wishlist';

const SingleCard = ({ main_img, second_img, title, price, prev_price, id, currencyIcon, allData , heartColor , heartStyle }) => {
    const {addItem} = useCart();
    const { addWishlistItem } = useWishlist();
    const {getWishlistItem , removeWishlistItem} = useWishlist();
    
    return (
            <div className="card-main">

                <div className="image-box">
                    <LinkContainer to={`/product/${id}`}>
                        <img className='main-img' src={main_img} alt="" />
                    </LinkContainer>
                    <img className='second-img' src={second_img} alt="" />

                    <div className="hover-buttons-main">
                        <button className='btn-hover' title='Add To Cart' onClick={()=>{
                            addItem(allData);
                        }}
                        > <i className="fa-solid fa-cart-shopping"></i>
                         </button>

                        <LinkContainer to={`/product/${id}`}>
                            <button className='btn-hover'> <i className="fa-regular fa-eye"></i> </button>
                        </LinkContainer>

                        <button className='btn-hover' onClick={() => {
                            navigator.clipboard.writeText(main_img);
                        }}>
                            <i className="fa-solid fa-link"></i>
                        </button>

                        <button className='btn-hover' onClick={
                            ()=>{
                                if(getWishlistItem(id)!==undefined){
                                    removeWishlistItem(id);
                                }else{
                                    addWishlistItem(allData);
                                }
                            }
                            }>
                            <i className= {`fa-${heartStyle} fa-heart text-${heartColor}`}></i>
                        </button>
                    </div>

                </div>

                <LinkContainer to={`/product/${id}`}>
                    <div className="text-box">
                        <p className='title'>{title}</p>
                        <p className='price'>{currencyIcon} {price} <del>{prev_price}</del></p>
                    </div>
                </LinkContainer>

            </div>
    )
}

export default SingleCard