import React, { useContext, useEffect, useState } from 'react'
import { LanguageContext } from '../context/LaguageContext';
import { LinkContainer } from 'react-router-bootstrap';

const HeroPart = ({ textHead, subHead }) => {

  // Language Context
  const [languageData] = useContext(LanguageContext);
  const [headerData, setHeaderData] = useState({});

  useEffect(() => {
    if (languageData.header !== undefined) {
      setHeaderData(languageData.header)
    }
  },)

  return (
    <div className='hero-part'>
      <h5>{textHead}</h5>
      <div className="text-bottom-main">

        <LinkContainer to="/">
          <span>{headerData.home}</span>
        </LinkContainer>

        <span> / </span>

        <span>{subHead}</span>
      </div>
    </div>
  )
}

export default HeroPart