import React, { useContext, useEffect, useState } from 'react'
import { LanguageContext } from '../context/LaguageContext';

const Footer = () => {
    // Language Context
    const [languageData] = useContext(LanguageContext);
    //
    const [footerData, setFooterData] = useState({}); 
    const [buttons , setButtons] = useState({});
    useEffect(() => {
        if (languageData.footer !== undefined) {
            setFooterData(languageData.footer)
        }
        // Buttons
        if (languageData.buttons !== undefined) {
            setButtons(languageData.buttons);
        }
    },)

    return (
        <footer>
            <div className="footer-top">
                <div className="row g-3">
                    <div className="col-12 col-sm-12 col-md-4" id='news-col'>
                        <div className="left-footer-top container">
                            <h3>NewsLetter</h3>
                            <p>Lorem ipsum dolor</p>
                        </div>
                    </div>

                    <div className="col-12 col-sm-12 col-md-4" id='input-col'>
                        <div className="input-container">
                            <div className="email-input">
                                <input type="email" name="" id="" placeholder='Email adress' />
                                <button>
                                    <span>{buttons.subscribe}</span>
                                </button>
                            </div>
                        </div>
                    </div>

                    <div className="col-12 col-sm-12 col-md-4" id='col-social-media'>
                        <div className="social-media">
                            <button>
                                <i className="fa-brands fa-twitter"></i>
                            </button>

                            <button>
                                <i className="fa-brands fa-facebook-f"></i>
                            </button>

                            <button>
                                <i className="fa-brands fa-pinterest"></i>
                            </button>

                            <button>
                                <i className="fa-brands fa-google"></i>
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <div className="footer-bottom">
                <div className="container-fluid">
                    <div className="row g-5">
                        <div className="col-12 col-sm-6 col-md-3">
                            <div className="left-footer-bottom">
                                <img src="https://cdn.shopify.com/s/files/1/0013/5567/7770/files/marley-logo.png?v=1613531603" alt="" />
                                <p>{footerData.col_1_text}</p>
                            </div>
                        </div>

                        <div className="col-12 col-sm-6 col-md-3">
                            <div className="footer-bottom-nav-list">
                                <ul>
                                    <li className='list-head'><h4>{footerData.col_2_text_head}</h4></li>
                                    <li><a href="*">{footerData.col_2_text_item_1}</a></li>
                                    <li><a href="*">{footerData.col_2_text_item_2}</a></li>
                                    <li><a href="*">{footerData.col_2_text_item_3}</a></li>
                                    <li><a href="*">{footerData.col_2_text_item_4} Policy</a></li>
                                    <li><a href="*">{footerData.col_2_text_item_5} Details</a></li>

                                </ul>
                            </div>
                        </div>

                        <div className="col-12 col-sm-6 col-md-3">
                            <div className="footer-bottom-nav-list">
                                <ul>
                                    <li className='list-head'><h5>{footerData.col_3_text_head}</h5></li>
                                    <li><a href="*">{footerData.col_3_text_item_1}</a></li>
                                    <li><a href="*">{footerData.col_3_text_item_2}</a></li>
                                    <li><a href="*">{footerData.col_3_text_item_3}</a></li>
                                    <li><a href="*">{footerData.col_3_text_item_4}</a></li>
                                    <li><a href="*">{footerData.col_3_text_item_5}</a></li>

                                </ul>
                            </div>
                        </div>

                        <div className="col-12 col-sm-6 col-md-3">
                            <div className="footer-bottom-nav-list">
                                <ul>
                                    <li className='list-head'><h4>{footerData.col_4_text_head}</h4></li>

                                    <li>
                                        <p>No: 58 A, East Madison Street, Baltimore, MD, USA 4508</p>
                                    </li>

                                    <li>
                                        <i className="fa-solid fa-phone"></i>
                                        <span>+91 12345678</span>
                                    </li>

                                    <li>
                                        <i className="fa-solid fa-envelope"></i>
                                        <span>support@mail.com</span>
                                    </li>

                                </ul>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </footer>
    )
}

export default Footer